<template>
	<div class="app-container">
		        <inputs-header
				:isAdmin="isAdmin"
				options="[]"
                @btnClick="checkIccid"
                class="clearfix"></inputs-header>
		<el-table :data="tableData" border v-loading="isLoad" style="width: 100%">
			<el-table-column align="center" prop="number" label="接入号码" width="140">
			</el-table-column>
			<el-table-column align="center" prop="iccid" label="ICCID" width="200">
			</el-table-column>
			<el-table-column align="center" prop="orderId" label="订单号" width="150">
			</el-table-column>
			<el-table-column align="center" prop="payOrderId" label="交易单号">
			</el-table-column>
			<el-table-column align="center" prop="time" label="交易时间" width="120">
			</el-table-column>
			<el-table-column align="center" prop="content" width="200" label="订单内容">
			</el-table-column>
			<el-table-column align="center" prop="price" label="金额(元)" width="100">
			</el-table-column>
		</el-table>
		<div style="text-align:center;margin: 20px auto;">
			<el-pagination style="display: inline-block" :page-size="10" @current-change="pageChange" :current-page.sync="currentPage" layout="prev, pager, next, jumper" :total="tableCount">
			</el-pagination>
		</div>
	</div>
</template>


<script>
/* eslint-disable quotes,indent,keyword-spacing,spaced-comment,no-var */
import InputsHeader from '../../../components/InputsHeader/index';
import {
	isNumber
} from '../../../utils/validate';
import {
	getListPayOrderByMid,
		getListPayOrderByIccid
} from '../../../api/admin/payOrderInfor';
export default {
	data() {
		return {
			tableData: [],
			tableCount: 0,
			currentPage: 1,
			isAdmin:false,
			options:[],
			isLoad: true,
			cur_iccid: ''
		}
	},
	watch: {},
	mounted() {
		this.isLoad=true
       this.ListPayOrderByMid(1)
	},
	methods:  {
			checkIccid(iccid){
				this.tableData = [];
				this.isLoad = true;
				this.isMid = false;
				this.cur_iccid = iccid;
				this.currentPage=1
				iccid?this.ListPayOrderByIccid(iccid, 1):this.ListPayOrderByMid(1);
			},
			pageChange(page){
				this.isLoad = true;
				if (this.cur_iccid.length>0) {
					this.ListPayOrderByIccid(this.cur_iccid, page);
				} else {
					 this.ListPayOrderByMid(page)
				}
			},
			ListPayOrderByMid(page){
				getListPayOrderByMid({page, num: 10})
					.then(res => {
						this.isLoad = false;
						if (res.msg == 0) {
							this.tableData = res.payOrderList;
							if (page === 1) {
								this.tableCount = res.count;
							}
						} else {
							this.tableData = [];
							this.tableCount = 0;
						}
					});
			},
			ListPayOrderByIccid(iccid, page){
				getListPayOrderByIccid({iccid, page, num: 10})
					.then(res => {
						this.isLoad = false;
						if (res.msg == 0) {
							this.tableData = res.payOrderList;
							if (page === 1) {
								this.tableCount = res.count;
							}
						} else {
							this.tableData = [];
							this.tableCount = 0;
						}
					})
			}
		},
		components: {
			InputsHeader
		}
	};
</script>

<style scoped>
.app-container {
	background: #f5f5f5;
}
</style>



